<template>
    <div class="branddetail">
        <van-nav-bar title="不凡" left-text="返回" left-arrow @click-left="onClickLeft" />
        <img width="100%" height="160px" src="http://yanxuan.nosdn.127.net/1541445967645114dd75f6b0edc4762d.png" alt="">
        <div class="inner">
            <h3>{{list.name}}</h3>
        </div>
        <div class="txt">
            {{list.simple_desc}}
        </div>
        <div class="mask">
            数据库暂无数据...
        </div>
        <div class="goods-list">
            <div class="goods-item" @click="goInfo(item.id)" v-for="item in goodsList" :key="item.id">
                <img :src="item.list_pic_url" alt="">
                <p>{{item.name}}</p>
                <span>￥{{item.retail_price}}</span>
            </div>
        </div>
    </div>
</template>

<script>
    import {
        branddetail
    } from '@/api/home/branddetail/index.js'
    export default {
        data() {
            return {
                list: [],
                goodsList: [],
            };
        },
        computed: {

        },
        created() {

        },
        mounted() {
            branddetail({
                id: this.$route.query.id
            }).then(res => {
                this.list = res.data.data;
                this.goodsList = res.data.goodsList;
                console.log(this.goodsList);
            })
        },
        methods: {
            onClickLeft() {
                history.back()
            },
            goInfo(id) {
                this.$router.push(`/category/goods/info?id=${id}`)
            },
        },
    };
</script>

<style scoped lang="scss">
    .branddetail {
        height: 810px;

        .inner {
            width: 100px;
            height: 25px;
            position: absolute;
            top: 135px;
            left: 50%;
            margin-left: -50px;
            margin-top: -40px;
            text-align: center;

            h3 {
                font-weight: normal;
            }

            font-size: 16px;
            color: #fff;
            border-bottom: 3px solid #fff;

        }

        .txt {
            background: #fff;
            padding: .56rem .42667rem;
            font-size: .4rem;
            color: #666;
            text-align: center;
        }

        .goods-list {
            background-color: #f4f4f4;
            position: absolute;
            top: 330px;
            left: 0;
            margin-top: 10px;
            display: flex;
            justify-content: space-between;
            flex-wrap: wrap;

            .goods-item {
                width: 49%;
                background-color: #fff;
                margin-bottom: 5px;
                padding: 10px 0px;

                img {
                    display: block;
                    margin: 0px auto;
                    width: 166px;
                    height: 166px;
                }

                p {
                    font-size: 14px;
                    margin: 10px 0px;
                    text-align: center;
                }

                span {
                    display: block;
                    width: 100%;
                    text-align: center;
                    color: #b4282d;
                    margin: 10px 0px;
                    font-size: 14px;
                }
            }
        }

        .mask {
            width: 100%;
            height: 40px;
            line-height: 40px;
            text-align: center;
            color: #999;
            font-size: 16px;
            margin-top: 100px;
        }
    }
</style>